                            </div>
                            <div class="citys-box">
                                <p class="citys-title greenUp">Meta Paris 1X1 IN SANDBOX</p>
                                <div class="citys-item">
                                    <div class="item-left-side">
                                        <img src="@/assets/imgs/city/left-city-18.png" alt="" class="img-left-city">
                                    </div>
                                    <div class="item-right-side">
                                        <p class="item-right-title">Meta Paris</p>
                                        <div class="item-right-icons">
                                            <img src="@/assets/imgs/logo.png" alt="" class="img-citys-icon">
                                            <img src="@/assets/imgs/city/left-logo-18.png" alt="" class="img-citys-icon">
                                        </div>
                                        <div class="item-right-city">
                                            <img src="@/assets/imgs/city/min-city-18.png" alt="" class="img-city" >
                                        </div>
                                        <p class="item-right-size"><span class="size-bg">&nbsp;</span> <span> land size: 1x1</span></p>
                                        <p class="item-right-position"><span class="position-bg">&nbsp;</span> <span>landmark: -182, -33</span></p>
                                        <p class="item-right-button"><a href="https://www.sandbox.game/en/map/?liteMap=true&currentX=-112&currentY=1585&zoom=0.5&x=-182&y=-33" target="_blank">Click to cities in sandbox</a></p>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div v-show="currentPage == 4">
                            <div class="citys-box">
                                <p class="citys-title greenUp">Beauteeverse 1X1 IN SANDBOX</p>
                                <div class="citys-item">
                                    <div class="item-left-side">
                                        <img src="@/assets/imgs/city/left-city-19.png" alt="" class="img-left-city">
                                    </div>
                                    <div class="item-right-side">
                                        <p class="item-right-title">Beauteeverse</p>
                                        <div class="item-right-icons">
                                            <img src="@/assets/imgs/logo.png" alt="" class="img-citys-icon">
                                            <img src="@/assets/imgs/city/left-logo-19.png" alt="" class="img-citys-icon">
                                        </div>
                                        <div class="item-right-city">
                                            <img src="@/assets/imgs/city/min-city-19.png" alt="" class="img-city" >
                                        </div>
                                        <p class="item-right-size"><span class="size-bg">&nbsp;</span> <span> land size: 1x1</span></p>
                                        <p class="item-right-position"><span class="position-bg">&nbsp;</span> <span>landmark: 193, -187</span></p>
                                        <p class="item-right-button"><a href="https://www.sandbox.game/en/beta-map/?lat=-188&lng=197&zoom=7&x=193&y=-187" target="_blank">Click to cities in sandbox</a></p>
                                    </div>
                                </div>
                            </div>
                        </div>
                        
                    </div>
                    <!--城市列表 start-->

                    <div class="content-pages">
                        <div class="page-box">
                            <a class="page-link" @click="changePage('up')"><img src="@/assets/imgs/page-up.png" class="img-page-up" alt=""></a>
                            <a :class="currentPage == 1?'page-link active':'page-link'" @click="changePage(1)">1</a>
                            <a :class="currentPage == 2?'page-link active':'page-link'" @click="changePage(2)">2</a>
                            <a :class="currentPage == 3?'page-link active':'page-link'" @click="changePage(3)">3</a>
                            <a :class="currentPage == 4?'page-link active':'page-link'" @click="changePage(4)">4</a>
                            <a class="page-link" @click="changePage('next')"><img src="@/assets/imgs/page-next.png" class="img-page-next" alt=""></a>
                        </div>
                    </div>
                    <!--城市列表 end-->
                    <!--footer start-->
                    <Footer />
                    <!--footer end-->
                    
                </div>
                <!--中间部分 end-->
                
            </div>
        </div>
        <!--中间内容 end--->
    </div>
</template>
<script>
import Header from '@/components/header'
import Footer from '@/components/footer'
import Menu from '@/components/menu'
export default {
    name: 'Citys',
    components:{
        Header,
        Footer,
        Menu
    },
    data(){
        return {
            currentPage:1,
            cityList:[],
        }
    },
    mounted(){
        this.returnTop();
    },
    methods:{
        returnTop(){
            document.querySelector("#contents").scrollIntoView(true);
            
        },
        changePage(page){
            let _this = this;
            switch(page){
                case 'up':
                    if(_this.currentPage != 1 ){
                        _this.currentPage = _this.currentPage - 1;
                    }
                    break;
                case 'next':
                    if(_this.currentPage != 4 ){
                        _this.currentPage = _this.currentPage + 1;
                    }
                    break;
                default:
                    _this.currentPage = Number(page);
                    break;

            }
        }
    }
}
</script>
<style scoped lang="less">



</style>



